export enum TabNames {
    Common = "common",
    ModbusServer = "modbus-server",
    ModbusClient = "modbus-client",
    ModbusRS0 = "modbus-rs-485-0",
    ModbusRS1 = "modbus-rs-485-1",
    ModbusGetway = "modbus-getway",
    Ethernet = "ethernet",
    Wifi = "wifi",
    Overvis = "overvis",
}

export const TAB_PANEL_ID = "settings-nav-menu";

export function selectTabByTabName(tabName: TabNames): void {
    const tabPanelContainer = document.querySelector(`#${TAB_PANEL_ID}`) as HTMLElement;

    if (!tabPanelContainer.querySelector(`[data-tab-name="${tabName}"]`)) {
        return;
    }
    // deactivate current tab
    tabPanelContainer.querySelector(".nav-link.active")?.classList.remove("active");

    // activate current tab
    tabPanelContainer.querySelector(`[data-tab-name="${tabName}"]`)?.classList.add("active");

    if (
        tabName === TabNames.ModbusGetway ||
        tabName === TabNames.ModbusRS0 ||
        tabName === TabNames.ModbusRS1 ||
        tabName === TabNames.ModbusServer ||
        tabName === TabNames.ModbusClient
    ) {
        tabPanelContainer.querySelector(`#modbus-tab`)?.classList.add("active");
    } else if (tabName === TabNames.Ethernet || tabName === TabNames.Wifi) {
        tabPanelContainer.querySelector(`#internet-tab`)?.classList.add("active");
    } else {
        // activate tab
        tabPanelContainer.querySelector(`#${tabName}-tab`)?.classList.add("active");
    }

    // deactivate currect form sector
    document.querySelector(".tab-pane.show.active")?.classList.remove("show");
    window.setTimeout(() => {
        document.querySelector(".tab-pane.active")?.classList.remove("active");

        // activate form sector
        document.querySelector(`#${tabName}-form-sector`)?.classList.add("show", "active");
    }, 250);
}

export function rigSaveSelectedTabToSession(): void {
    const tabs = Array.from(document.querySelectorAll('[data-bs-toggle="tab"]'));
    for (const tab of tabs) {
        tab.addEventListener("click", () => {
            const tabName = tab.getAttribute("data-tab-name") as TabNames;
            sessionStorage.setItem("tabName", tabName);
        });
    }
}

export function checkSavedTabName(): void {
    const tabName = sessionStorage.getItem("tabName") as TabNames | undefined;
    if (!tabName) {
        return;
    }
    selectTabByTabName(tabName);
}
