import { ControlLoadError } from "../../errors";
import { loadListSer } from "../../data/load";
import { catchAsync, fillSelect } from "../../help";

const RS_485_0_NETIF_SELECT_ID = "rs-485-device-name";
const RS_485_1_NETIF_SELECT_ID = "rs-485-1-device-name";
const RS_485_NETIF_UPDT_BTN = "rs485-ssid-update-btn";
const RS_485_1_NETIF_UPDT_BTN = "rs485-1-ssid-update-btn";

export function rigUpdateBtnsRs485NetifList(): void {
    const updateBtn0 = document.querySelector(`#${RS_485_NETIF_UPDT_BTN}`) as HTMLElement;
    updateBtn0.addEventListener(
        "click",
        catchAsync(async () => {
            updateBtn0.querySelector(".bi-arrow-clockwise")?.classList.add("d-none");
            updateBtn0.querySelector(".spinner-border")?.classList.remove("d-none");
            await fillRs4850NetifLists();
            updateBtn0.querySelector(".spinner-border")?.classList.add("d-none");
            updateBtn0.querySelector(".bi-arrow-clockwise")?.classList.remove("d-none");
        }),
    );

    const updateBtn1 = document.querySelector(`#${RS_485_1_NETIF_UPDT_BTN}`) as HTMLElement;
    updateBtn1.addEventListener(
        "click",
        catchAsync(async () => {
            updateBtn1.querySelector(".bi-arrow-clockwise")?.classList.add("d-none");
            updateBtn1.querySelector(".spinner-border")?.classList.remove("d-none");
            await fillRs4851NetifLists();
            updateBtn1.querySelector(".spinner-border")?.classList.add("d-none");
            updateBtn1.querySelector(".bi-arrow-clockwise")?.classList.remove("d-none");
        }),
    );
}

export async function fillRs4850NetifLists(): Promise<void> {
    const errorControl = new ControlLoadError();
    const list = await loadListSer(errorControl);

    fillSelect(
        RS_485_0_NETIF_SELECT_ID,
        list.map((i) => i.devName),
    );
}

export async function fillRs4851NetifLists(): Promise<void> {
    const errorControl = new ControlLoadError();
    const list = await loadListSer(errorControl);

    fillSelect(
        RS_485_1_NETIF_SELECT_ID,
        list.map((i) => i.devName),
    );
}
